'use strict';
require('reflect-metadata');

/** api token */
const apiPermissionMetadataKey = Symbol('api_permission_check');

// token
function checkApiPermission(isCheck = false) {
    return Reflect.metadata(apiPermissionMetadataKey, isCheck);
}
// 
function getApiPermission(target, propertyKey) {
    return Reflect.getMetadata(apiPermissionMetadataKey, target, propertyKey);
}
/* end api token */

module.exports = {
    apiPermissionMetadataKey,
    checkApiPermission,
    getApiPermission,
};
