'use strict';
require('reflect-metadata');
const { apiPermissionMetadataKey } = require('./jvCommonPermission')
const apiDecorator = require('@jv/jv-models/lib/decorator');

/** api token */
const apiTokenMetadataKey = 'common/api_access_toke';
const apiAuthMetadataKey = 'common/api_access_auth';
const paramRequireKey = 'common/api_param_require';

// token
function checkApiToken(isCheck = true) {
    return Reflect.metadata(apiTokenMetadataKey, isCheck);
}
// 
function getApiToken(target, propertyKey) {
    return Reflect.getMetadata(apiTokenMetadataKey, target, propertyKey);
}

// 
function checkApiLogin(isCheck = true) {
    return Reflect.metadata(apiAuthMetadataKey, isCheck);
}
// 
function getApiLogin(target, propertyKey) {
    return Reflect.getMetadata(apiAuthMetadataKey, target, propertyKey);
}

// 
function paramRequire(isRequire = true) {
    return Reflect.metadata(paramRequireKey, isRequire);
}
// 
function getParamRequire(target, propertyKey) {
    return Reflect.getMetadata(paramRequireKey, target, propertyKey);
}

function checkApiMetadata(options) {
    return (target, propertyKey) => {
        const { checkToken, checkLogin, checkPermission } = options
        if (typeof checkToken === 'boolean') {
            Reflect.defineMetadata(apiTokenMetadataKey, checkToken, target, propertyKey);
        }
        if (typeof checkLogin === 'boolean') {
            Reflect.defineMetadata(apiAuthMetadataKey, checkLogin, target, propertyKey);
        }
        if (typeof checkPermission === 'boolean') {
            Reflect.defineMetadata(apiPermissionMetadataKey, checkPermission, target, propertyKey);
        }
    }
}

/* end api token */

module.exports = {
    checkApiMetadata,
    checkApiToken,
    getApiToken,
    checkApiLogin,
    getApiLogin,
    paramRequire,
    getParamRequire,
    api: apiDecorator.api,
    getApi: apiDecorator.getApi,
    apiDecorator,
};

